<template>
	<div class="container">
		<v-back title="请假实习" />
		<div class="scroll-view">
			<md-field>
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">类型</label>
						<div class="md-field-item-control">
							<md-radio-group v-model="type">
								<md-radio-box name="1" style="margin-right: 5px;">请假</md-radio-box>
								<md-radio-box name="2">实习</md-radio-box>
							</md-radio-group>
						</div>
					</div>
				</div>
				
				<md-field-item title="* 开始时间" :content="no_attend_starttime" @click="showSelector1" arrow solid />
				<md-date-picker
				    ref="datePicker"
				    v-model="isSelectorShow1"
				    title="选择日期"
				    large-radius
				    :default-date="currentDate1"
				    @confirm="onSelectorConfirm1" >
				</md-date-picker>
				
				<md-field-item title="* 结束时间" :content="no_attend_endtime" @click="showSelector2" arrow solid />
				<md-date-picker
				    ref="datePicker2"
				    v-model="isSelectorShow2"
				    title="选择日期"
				    large-radius
				    :default-date="currentDate2"
				    @confirm="onSelectorConfirm2" >
				</md-date-picker>
				
				<div class="md-field-item is-solid">
					<div class="md-field-item-content" style="padding: 0;">
						<label class="md-field-item-title">证明图片</label>
						<div class="md-field-item-control">
							<ul class="image-reader-list">
								<li
								class="image-reader-item"
								v-for="(img, index) in imageList['tupian']"
								:key="index"
								:style="{
									'backgroundImage': `url(${img})`,
									'backgroundPosition': 'center center',
									'backgroundRepeat': 'no-repeat',
									'backgroundSize': 'cover'
								}"
								@click="imgsShow(img)">
								<md-tag
									class="image-reader-item-del"
									size="small"
									shape="quarter"
									fill-color="#111A34"
									type="fill"
									font-color="#fff"
									@click.native.stop="onDeleteImage('tupian', index)"
								>
									<md-icon name="close"></md-icon>
								</md-tag>
								</li>
								<li class="image-reader-item add" v-show="imageList.tupian.length == 0">
									<md-image-reader
										name="tupian"
										@select="onReaderSelect"
										@complete="onReaderComplete"
										@error="onReaderError"
									>
									</md-image-reader>
									<md-icon name="camera" size="md" color="#CCC"></md-icon>
									<p>添加图片</p>
								</li>
							</ul>
						</div>
					</div>
				</div>
				
				
				
			</md-field>
			<md-field>
				<md-button type="primary" size="small" round @click="submit">提交</md-button>
			</md-field>
		</div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	import dayjs from 'dayjs'
	import axios from 'axios'
	import compressImage from 'ls-compressimage'
	export default {
		name: 'apply_index',
		data() {
			return {
				isViewerShow: false,
				imgs: [],
				imageList: {
				    tupian: []
				},
				blob: null,
				file: {},
				
				type: '1',
				no_attend_starttime: dayjs().format('YYYY/MM/DD'),
				no_attend_endtime: dayjs().format('YYYY/MM/DD'),
				
				currentDate1: new Date(),
				currentDate2: new Date(),
				
				isSelectorShow1: false,
				isSelectorShow2: false,
				
				user: JSON.parse(localStorage.getItem('user'))
			}
		},
		methods: {
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			showSelector1() {
			    this.isSelectorShow1 = true
			},
			onSelectorConfirm1() {
			    this.no_attend_starttime = this.$refs.datePicker.getFormatDate('yyyy/MM/dd')
			},
			showSelector2() {
			    this.isSelectorShow2 = true
			},
			onSelectorConfirm2() {
			    this.no_attend_endtime = this.$refs.datePicker2.getFormatDate('yyyy/MM/dd')
			},
			
			onReaderSelect(name, {files}) {
			    files.forEach(file => {
			        console.log('[Mand Mobile] ImageReader Selected:', 'File Name ' + file.name)
			    })
			    this.$toast.loading('图片读取中...')
			},
			onReaderComplete(name, {dataUrl, blob, file}) {
			    this.$toast.hide()
			    console.log('[Mand Mobile] ImageReader Complete:', 'File Name ' + file.name)
			    setTimeout(() => {
			        const demoImageList = this.imageList[name] || []
			        demoImageList.push(dataUrl)
			        this.$set(this.imageList, name, demoImageList)
			        this.blob = blob
			        this.file = file
			    }, 100)
			},
			onReaderError(name, {msg}) {
			    this.$toast.failed(msg)
			},
			onDeleteImage(name, index) {
			    const demoImageList = this.imageList[name] || []
			    demoImageList.splice(index, 1)
			    this.$set(this.imageList, name, demoImageList)
			},
			submit() {
				let params = {
					user_id: this.user.user_id,
					user_nbr: this.user.usernbr,
					user_name: this.user.username,
					type: this.type,
					photo: '',
					no_attend_starttime: this.no_attend_starttime,
					no_attend_endtime: this.no_attend_endtime
				}
				
				//return;
				if (this.blob == null || this.imageList.tupian.length == 0) {
					this.updateStudent(params)
				} else {
					this.$toast.loading('图片上传中...');
					new compressImage({
						res: this.file, // 必选 资源
						justConversion: false, // 可选 声明了此字段说明只进行格式转换 res requestType responseType callback必填，其他随意
						width: 300, // 可选(justConversion存在可不选) (默认图片自身宽度,建议自定义) 压缩后图片宽度
						//height: 300,//可选 (默认 自定义图片宽度/原生图片宽度*原生图片高度) 压缩后图片高度
						quality: 0.1, // 可选 (默认0.3) 压缩层级质量（0<quality<1）越大压缩越快 图片质量跨度相对低 越小则相反
						size: 500, // 必选(justConversion存在可不选) 压缩后图片大小 单位k
						requestType: 'file', // 必选 传入文件类别 base64 | file | blob
						responseType: 'file', // 必选 返回文件类别 base64 | file | blob
						fileName: 'yellow', // 如果responseType的类型为file和非justConversion 此选项可选 返回文件对象的文件名
						callback: (result) => {
						  if (result === null) {
							this.$toast.failed("图片上传失败")
							return
						  }
						  var formData = new FormData();
						  formData.append("file", result, new Date().getTime() + '.' + result.type.split('/')[1]);
						  axios.post('http://106.12.3.212/dorm/yellow/UploadImg/-1', formData, { headers: {'Content-Type': 'multipart/form-data'} }).then( res => {
							  if (res.data.code == 0) {
								  params.photo = res.data.url
								  setTimeout(() => {
									this.updateStudent(params)
								  }, 500)
							  } else {
								  this.$toast.failed("图片上传失败")
							  }
						  }).catch((err) => {
							this.$toast.failed("图片上传失败")
						  })
						}
					})
				}
			},
			updateStudent(params) {
				this.$toast.loading('数据提交中...');
				console.log(params)
				this.$post('/apply/add', params, ()=>{
				    this.$toast.succeed("提交成功")
					setTimeout(() => {
						this.$router.back()
					}, 500)
				})
			},
		},
		mounted() {
			
		}
	}
</script>

<style scoped="scoped">
	.image-reader-list {
	     width: 100%;
	     margin-top: 3vw;
		 clear: both;
	 }
	 .image-reader-list .image-reader-item {
	     position: relative;
	     width: 25vw;
	     height: 25vw;
	     padding-bottom: 23.5%;
	     margin-bottom: 2%;
	     margin-right: 2%;
	     background: #FFF;
	     box-shadow: 0 5px 20px rgba(197, 202, 213, .25);
	     box-sizing: border-box;
	     list-style: none;
	     border-radius: 4px;
	     background-size: cover;
	     overflow: hidden;
	 }
	 .image-reader-list .image-reader-item .md-tag {
	     position: absolute;
	     right: 0;
	 }
	 .image-reader-item.add .md-icon {
	      position: absolute;
	      top: 40%!important;
	      left: 50%;
	      transform: translate(-50%, -50%);
	      opacity: .5;
	  }
	 .image-reader-item.add p {
	      position: absolute;
	      top: 35%;
	      left: 0;
	      width: 100%;
	      margin-top: 15px;
	      font-size: 22px;
	      color: #CCC;
	      text-align: center;
	      font-size: 12px;
	  }
	 .md-drop-menu {
		 position: relative;
	 }
	 .md-drop-menu >>> .md-popup {
		 z-index: 1201;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar {
		 background: none;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar:before {
		 display: none;
	 }
	 .md-drop-menu >>> .md-drop-menu-bar .bar-item {
		 justify-content: left;
	 }
	 .scroll-view {
		height: calc(100vh - 10vw);
		overflow-y: auto;
	 }
</style>
